
const TimelineModes = ['left', 'alternate', 'right', 'custom'] as const;
export type TDSTimelineMode = typeof TimelineModes[number];

const TimelinePositions = ['left', 'right'] as const;
export type TDSTimelinePosition = typeof TimelinePositions[number];

export const TimelineTimeDefaultColors = ['primary', 'secondary', 'info', 'warning', 'error','success'] as const;
export type TDSTimelineItemColor = typeof TimelineTimeDefaultColors[number];
