
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  QueryList,
  SimpleChange,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { Direction, Directionality } from '@angular/cdk/bidi';
import { TDSTimelineItemComponent } from './timeline-item.component';
import { TimelineService } from './timeline.service';
import { TDSTimelineMode, TDSTimelinePosition } from './typings';
import { TDSHelperObject } from 'tds-ui/shared/utility';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  selector: 'tds-timeline',
  providers: [TimelineService],
  exportAs: 'tdsTimeline',
  templateUrl: './timeline.component.html',
  // template: `
  //   <ul
  //     class="tds-timeline"
  //     [class.tds-timeline-right]="tdsMode === 'right'"
  //     [class.tds-timeline-alternate]="tdsMode === 'alternate' || tdsMode === 'custom'"
  //     [class.tds-timeline-pending]="!!tdsPending"
  //     [class.tds-timeline-reverse]="tdsReverse"
  //     [class.tds-timeline-rtl]="dir === 'rtl'"
  //   >
  //     <!-- pending dot (reversed) -->
  //     <ng-container *ngIf="tdsReverse" [ngTemplateOutlet]="pendingTemplate"></ng-container>
  //     <!-- timeline items -->
  //     <ng-container *ngFor="let item of timelineItems">
  //       <ng-template [ngTemplateOutlet]="item.template"></ng-template>
  //     </ng-container>
  //     <ng-container *ngIf="!tdsReverse" [ngTemplateOutlet]="pendingTemplate"></ng-container>
  //     <!-- pending dot -->
  //   </ul>
  //   <ng-template #pendingTemplate>
  //     <li *ngIf="tdsPending" class="tds-timeline-item tds-timeline-item-pending">
  //       <div class="tds-timeline-item-tail"></div>
  //       <div class="tds-timeline-item-head tds-timeline-item-head-custom tds-timeline-item-head-blue">
  //         <ng-container *tdsStringTemplateOutlet="tdsPendingDot">
  //           {{ tdsPendingDot }}
  //           <tds-spinner class="w-5 h-5 mr-2.5"></tds-spinner>
  //         </ng-container>
  //       </div>
  //       <div class="tds-timeline-item-content">
  //         <ng-container *tdsStringTemplateOutlet="tdsPending">
  //           {{ isPendingBoolean ? '' : tdsPending }}
  //         </ng-container>
  //       </div>
  //     </li>
  //   </ng-template>
  //   <!-- Grasp items -->
  //   <ng-content></ng-content>
  // `
})
export class TDSTimelineComponent implements AfterContentInit, OnChanges, OnDestroy, OnInit {
  @ContentChildren(TDSTimelineItemComponent) listOfItems!: QueryList<TDSTimelineItemComponent>;

  @Input() tdsMode: TDSTimelineMode = 'left';
  @Input() tdsPending?: string | boolean | TemplateRef<void>;
  @Input() tdsPendingDot?: string | TemplateRef<void>;
  @Input() tdsReverse: boolean = false;

  isPendingBoolean: boolean = false;
  timelineItems: TDSTimelineItemComponent[] = [];
  dir: Direction = 'ltr';
  hasLabelItem = false;
  private destroy$ = new Subject<void>();

  constructor(
    private cdr: ChangeDetectorRef,
    private timelineService: TimelineService,
    @Optional() private directionality: Directionality
  ) { }

  ngOnChanges(changes: SimpleChanges): void {
    const { tdsMode, tdsReverse, tdsPending } = changes;

    if (simpleChangeActivated(tdsMode) || simpleChangeActivated(tdsReverse)) {
      this.updateChildren();
    }

    if (tdsPending) {
      this.isPendingBoolean = tdsPending.currentValue === true;
    }
  }

  ngOnInit(): void {
    this.timelineService.check$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.cdr.markForCheck();
    });

    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  ngAfterContentInit(): void {
    this.updateChildren();

    this.listOfItems.changes.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.updateChildren();
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private updateChildren(): void {
    if (this.listOfItems && this.listOfItems.length) {
      const length = this.listOfItems.length;
      let hasLabelItem = false;
      //check has lable item
      hasLabelItem = this.listOfItems.find(item => TDSHelperObject.hasValue(item?.tdsLabel)) ? true : false;
      // for (let index = 0; index < this.listOfItems.length; index++) {
      //   const item = this.listOfItems.get(index);
      //   if (!hasLabelItem && item?.tdsLabel) {
      //     hasLabelItem = true;
      //     break;
      //   }
      // }
      this.listOfItems.forEach((item, index) => {
        item.isLast = !this.tdsReverse ? index === length - 1 : index === 0;
        item.isPending = this.tdsPending != undefined && this.tdsPending !== false;
        item.isReverse = this.tdsReverse;
        item.isFirst =this.tdsReverse ? index === length - 1 : index === 0;
        item.position = getInferredTimelineItemPosition(index, this.tdsMode);
        item.mode = (hasLabelItem || this.tdsMode === 'alternate' || this.tdsMode === 'custom') ? 'alternate' : this.tdsMode;
        if (!hasLabelItem && item?.tdsLabel) {
          hasLabelItem = true;
        }
        item.detectChanges();
      });
      this.timelineItems = this.tdsReverse ? this.listOfItems.toArray().reverse() : this.listOfItems.toArray();
      this.hasLabelItem = hasLabelItem;
    } else {
      this.timelineItems = [];
      this.hasLabelItem = false;
    }
    this.cdr.markForCheck();
  }
}

function simpleChangeActivated(simpleChange?: SimpleChange): boolean {
  return !!(simpleChange && (simpleChange.previousValue !== simpleChange.currentValue || simpleChange.isFirstChange()));
}

function getInferredTimelineItemPosition(index: number, mode: TDSTimelineMode): TDSTimelinePosition | undefined {
  return mode === 'custom'
    ? undefined
    : mode === 'left'
      ? 'left'
      : mode === 'right'
        ? 'right'
        : mode === 'alternate' && index % 2 === 0
          ? 'left'
          : 'right';
}
