
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnChanges,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { TimelineService } from './timeline.service';
import { TDSTimelineItemColor, TDSTimelineMode, TDSTimelinePosition, TimelineTimeDefaultColors } from './typings';

function isDefaultColor(color?: string): boolean {
  return TimelineTimeDefaultColors.findIndex(i => i === color) !== -1;
}

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  selector: 'tds-timeline-item, [tds-timeline-item]',
  exportAs: 'tdsTimelineItem',
  templateUrl: './timeline-item.component.html',
  })
export class TDSTimelineItemComponent implements OnChanges {
  @ViewChild('template', { static: false }) template!: TemplateRef<void>;

  @Input() tdsPosition?: TDSTimelinePosition;
  @Input() tdsColor: TDSTimelineItemColor = 'primary';
  @Input() tdsDot?: string | TemplateRef<void>;
  @Input() tdsLabel?: string | TemplateRef<void>;
  isLast = false;
  isPending = false;
  isReverse = false;
  borderColor: string | null = null;
  position?: TDSTimelinePosition = 'left';
  mode: TDSTimelineMode = 'alternate';
  isFirst=false;
  constructor(private cdr: ChangeDetectorRef, private timelineService: TimelineService) { }

  ngOnChanges(changes: SimpleChanges): void {
    this.timelineService.markForCheck();
    if (changes.tdsColor) {
      this.updateCustomColor();
    }
  }

  detectChanges(): void {
    this.cdr.detectChanges();
  }

  private updateCustomColor(): void {
    this.borderColor = isDefaultColor(this.tdsColor) ? null : this.tdsColor;
  }
  getPositon() {
    return this.tdsPosition || this.position || 'left';
  }
}
